<template>
  <div class="list row">

    <div class="col-md-6">
      <h4>Owner List</h4>
      <ul class="list-group">
        <li
          class="list-group-item"
          :class="{ active: index == currentIndex }"
          v-for="(owner, index) in owners"
          :key="index"
          @click="setActiveOwner(owner, index)"
          >{{ owner.name }}
        </li>
      </ul>
      <button class="m-3 btn btn-sm btn-danger" @click="removeAllOwners">Remove All</button>
    </div>
    <div class="col-md-6">
      <div v-if="currentOwner">
        <h4>Owner</h4>
        <div>
          <label>
            <strong>ID:</strong>
          </label>
          {{ currentOwner.id }}
        </div>
        <div>
          <label>
            <strong>Owner:</strong>
          </label>
          {{ currentOwner.name }}
        </div>
        <div>
          <label>
            <strong>Age:</strong>
          </label>
          {{ currentOwner.age }}
        </div>

        <a class="badge badge-warning" :href="'/owner/' + currentOwner.id">Edit</a>
      </div>
      <div v-else>
        <br />
        <p>Please click on a Owner...</p>
      </div>
    </div>
  </div>
</template>
<script>
import OwnerDataService from "../services/OwnerDataService";

export default {
  name: "OwnerList",
  props: {
    msg: String
  },

  data: function() {
    return {
      owners_from_api: [],
      owners: [],
      currentOwner: null,
      currentIndex: -1,
      name: ""
    };
  },

  methods: {
    fetchOwners() {
      OwnerDataService.getAll()
        .then(response => {
          // JSON responses are automatically parsed.
          this.owners = response.data;
          console.log(response.data);
        })
        .catch(e => {
          this.errors.push(e);
          console.log(e);
        });
    },

    refereshList() {
      this.fetchOwners();
      this.currentOwner = null;
      this.currentIndex = -1;
    },
    
    setActiveOwner(owner, index) {
      this.currentOwner = owner;
      this.currentIndex = index;
    },

    removeAllOwners() {
      OwnerDataService.deleteAll()
        .then(response => {
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },

    searchName() {
      OwnerDataService.get(2)
        .then(response => {
          this.owners = response.data;
          console.log(this.owners);
        })
        .catch(e => {
          this.errors.push(e);
          console.log(e);
        });
    }
  },

  mounted() {
    this.fetchOwners();
  }
};
</script>

